@import url("https://fonts.googleapis.com/css?family=Montserrat");
@import url("https://fonts.googleapis.com/css?family=Acme|Anton|Patua+One");
@import url("https://fonts.googleapis.com/css?family=Crimson+Text|Work+Sans:400,700");
:root {
  --color-primary: #32aced;
  --color-primary-light: #02203c;
  --color-primary-lighter: #304150;
  --color-primary-dark: #1c2633;
  --color-secondary: #f8b71c;
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;
  --color-grey-dark-1: #333;
  --color-grey-dark-2: #777;
  --color-grey-dark-3: #999;
  --color-grey-dark-4: #a9a7a78a;
  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  --shadow-light: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  --line: 1px solid var(--color-grey-light-3); }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 48em) {
    html {
      font-size: 58%; } }
  @media only screen and (max-width: 26.56em) {
    html {
      font-size: 50%; } }

body {
  font-family: "Montserrat", sans-serif;
  color: var(--color-grey-light-1);
  line-height: 1.6;
  font-weight: 400;
  height: 100%; }

.container {
  max-width: 108rem;
  margin: 0 auto; }

.heading__primary {
  font-size: 4rem;
  color: var(--color-grey-light-4); }

.text-lead {
  color: var(--color-secondary);
  font-style: italic; }

.text-lead-sub {
  color: var(--color-secondary);
  text-align: center;
  margin-top: 4rem; }

.paragraph {
  color: var(--color-grey-light-2);
  font-size: 1.7rem;
  margin: 0 auto;
  width: 90%; }
  .paragraph--center {
    max-width: 80rem;
    text-align: center;
    margin: 0 auto; }
  .paragraph--small {
    font-size: 1.4rem; }

[data-scroll] {
  transition: all 2s; }

.portfolio__story[data-scroll="in"] {
  opacity: 1;
  transform: translateX(0); }

.portfolio__story[data-scroll="out"] {
  opacity: 0;
  transform: translateX(-200px); }

.project__image--box[data-scroll="in"] {
  transform: scale(1) skew(0); }

.project__image--box[data-scroll="out"] {
  transform: scale(0) skew(20deg); }

.heading__primary[data-scroll="in"] {
  opacity: 1;
  transform: translateY(0) scale(1); }

.heading__primary[data-scroll="out"] {
  opacity: 0;
  transform: translateY(100%) scale(0.5); }

.nav {
  height: 7rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000; }
  .nav__items {
    height: 100%;
    display: flex;
    max-width: 108rem;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto; }

.fixed-nav {
  position: fixed;
  background-color: #02203cc4;
  border-bottom: 1px solid var(--color-secondary); }

.logo__icon {
  text-decoration: none;
  margin-left: 3rem; }

.menu {
  height: 100%;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 3rem; }
  .menu__item {
    font-size: 1.4rem;
    height: 100%;
    display: flex;
    align-items: center;
    transition: all 0.2s ease; }
    .menu__item:not(:last-child) {
      margin-right: 3rem; }
    .menu__item--link, .menu__item--link:link, .menu__item--link:visited {
      cursor: pointer;
      text-decoration: none;
      color: var(--color-grey-light-2);
      z-index: 999; }
      .menu__item--link:hover, .menu__item--link:link:hover, .menu__item--link:visited:hover {
        padding-bottom: 7px;
        border-bottom: 2px solid var(--color-secondary); }

@media only screen and (max-width: 600px) {
  .nav {
    position: fixed;
    top: 0;
    left: -25rem;
    width: 25rem;
    height: 100%;
    transition: left 0.3s ease-in-out;
    opacity: 0;
    border-top: 4px solid var(--color-secondary);
    background-color: var(--color-primary-light);
    z-index: 1000; }
  .open {
    left: 0;
    opacity: 1; }
  .logo {
    width: 100%;
    margin: 0 auto; }
    .logo .logo__icon {
      margin: 0; }
  .nav {
    text-align: center; }
    .nav__items {
      flex-direction: column;
      justify-content: space-around;
      height: 70vh; }
    .nav .menu {
      flex: 0 0 50%;
      flex-direction: column;
      align-items: center;
      width: 100%;
      margin-right: 0; }
      .nav .menu__item {
        margin: 0;
        width: 100%;
        border-left: 3px solid var(--color-primary);
        background-color: var(--color-primary-lighter);
        position: relative;
        padding-left: 2rem; }
        .nav .menu__item:not(:last-child) {
          margin-bottom: 2rem; }
        .nav .menu__item--link:link, .nav .menu__item--link:visited {
          text-transform: uppercase;
          padding: 1.5rem 3rem;
          z-index: 10;
          width: 100%;
          text-align: start; }
        .nav .menu__item:hover {
          border: none; }
      .nav .menu__item::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 3px;
        background-color: var(--color-primary);
        transform: scaleY(0);
        transition: transform 0.2s, width 0.4s; }
      .nav .menu__item:hover::before {
        transform: scaleY(1);
        width: 100%;
        border-bottom: 2px solid var(--color-secondary); } }

/**** hamburger icon ****/
.hamburger {
  position: fixed;
  top: 1rem;
  right: 1.2rem;
  opacity: 1;
  z-index: 1001;
  display: none;
  background-color: #02203cc4;
  padding: 1.6rem;
  border-radius: 50%; }
  .hamburger:hover {
    cursor: pointer; }
  .hamburger span {
    display: block;
    background: #fff;
    margin: 6px 0;
    height: 3px;
    width: 36px;
    border-radius: 2px;
    position: relative;
    transition: all 0.5s ease-in-out; }
  @media only screen and (max-width: 600px) {
    .hamburger {
      display: block; } }

.change.first-bar {
  top: 8px;
  transform: rotate(45deg); }

.change.second-bar {
  opacity: 0; }

.change.third-bar {
  bottom: 10px;
  transform: rotate(-45deg); }

