.home-icon {
  fill: var(--color-primary); }

.landing__mouse {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%); }

.mouse {
  width: 3.5rem;
  height: 6rem;
  border: 2px solid var(--color-secondary);
  border-radius: 6rem; }

.mouse::before {
  content: "";
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 1.4rem;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background-color: var(--color-secondary);
  opacity: 1;
  animation: mouse 1.5s infinite; }

@keyframes mouse {
